cursor: pointer;
height: 170px;
width: 230px;
/* margin: 10px 10px; */
opacity: 0.6;
filter: alpha(opacity=50);

}
.video:hover {
  opacity: 1.0;
  filter: alpha(opacity=100); 
}
.flex{
  display: flex;
flex-wrap: wrap;

}
.frame{
  text-align: center;
  cursor: pointer;
}
.videoTitle{
  text-align: center;
  text-transform: uppercase;
  /* font-style: oblique; */
  /* font-family:  sans-serif; */
  text-shadow: 1px 1px rgb(8, 8, 8);
  color: #6c757d
}
/* .container{
  display: inline;
  float: left;
  width:33.3%;
  padding: 5px;
} */
.cover-page {
  height: 100vh;
  /* background: pink; */
}
.navDark {
  background: rgba(0, 0, 0, 0.6);
}

.nav-link {
  color: rgb(216, 216, 216) !important;
}

.nav-link.active,
.nav-link:hover {
  color: #fdd86a !important;
}

.subComponent {
  padding: 50px 0px;
}

.subComponent-lg {
  padding: 80px 0px;
}
#hero {
  background: url("./img/bg.jpg") center center;
  background-size: cover;
  background-attachment: fixed;
  color: white;
}
#hero h1 {
  font-family: "Raleway", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 900;
  font-size: 60px;
}
#packageBody {
  background-color: rgb(245, 245, 245);
}
.wrapped-page {
  width: 100%;
  height: 100vh;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  background: rgba(0, 0, 0, 0.3);
}
.item-center {
  text-align: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.img-carousel {
  width: 100%;
  height: 60vh;
  object-fit: cover;
}
.carousel-caption {
  height: 50%;
}
.carousel-caption h3 {
  font-size: 5em;
  font-weight: 700;
  text-shadow: 3px 3px 10px black;
}
.carousel-caption p {
  color: rgb(255, 239, 16);
  text-shadow: 2px 2px 5px black;
}
.card {
  margin: 10px 0px;
}
@media (min-width: 284px) {
  .card-columns {
    margin: 0px 20px;
  }
  .tour-msg {
    margin: 0px 10px;
  }
}
@media (min-width: 576px) {
  .card-columns {
    -webkit-column-count: 2 !important;
    -moz-column-count: 2 !important;
    column-count: 2 !important;
  }
  .tour-msg {
    margin: 0px 20px;
  }
}
@media (min-width: 986px) {
  .card-columns {
    -webkit-column-count: 3 !important;
    -moz-column-count: 3 !important;
    column-count: 3 !important;
  }
  .tour-msg {
    margin: 0px 50px;
  }
}
.packageBody .badge {
  margin: 0px 5px;
  cursor: pointer;
  text-transform: capitalize;
}
.tour-cover {
  width: 90%;
  padding: 20px 10px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
  margin: 50px auto;
}
.tour-cover img {
  width: 100%;
}
.tour-gallery img {
  width: 50%;
  height: 100px;
  object-fit: cover;
  padding: 5px;
}
.tour-msg input,
.tour-msg textarea {
  width: 100%;
  margin: 5px;
  border: 1px solid #6c757d;
  padding: 10px;
  border-radius: 5px;
  background: none;
}
.msg input,
.msg textarea {
  width: 100%;
  margin: 5px;
  border: 2px solid rgba(255, 255, 255, 0.7);
  padding: 10px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.3);
}
.msg .btn {
  margin: 5px;
}
.reviews {
  background: #f1f1f1;
  padding: 40px 0px 80px 0px;
  margin: 0px;
}
#aboutBody {
  background: #f1f1f1;
  color: #2b2b2b;
}
.svg-group p {
  margin: 15px 0px;
}
.svg-card {
  background: rgba(0, 0, 0, 0.1);
  color: #2b2b2b;
  padding: 30px;
  margin: 10px;
  width: 200px;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
}
.svg-card:hover {
  border-radius: 50%;
}
.wrapped-services {
  background-size: cover;
  background-attachment: fixed;
}
#servicesBody {
  background: rgba(0, 0, 0, 0.7);
}
#contactBody {
  background: #6d613c;
  color: rgba(0, 0, 0, 0.7);
}
#footerBody {
  background: #383322;
  color: rgba(0, 0, 0, 0.7);
}
#footerBody a {
  color: rgba(0, 0, 0, 0.6);
}
#footerBody a:hover {
  color: rgba(255, 255, 255, 0.6);
}
.svg-card-2 {
  color: #ffffff;
  padding: 30px;
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
}
.svg-card-2:hover {
  border-style: solid;
  border-width: 2px;
  border-color: #e0e0e0;
  background: rgba(255, 255, 255, 0.3);
}
.svg-card-3 {
  padding: 30px;
  margin: 10px;
  color: rgba(255, 255, 255, 0.7);
}
.headerTitle p {
  color: rgba(0, 0, 0, 0.5);
}
* {
  transition-duration: 0.3s;
}
.active-pink-4 input[type="text"]:focus:not([readonly]) {
  border: 1px solid gold;
  box-shadow: 0 0 0 1px goldenrod;
}
.active-pink-3 input[type="text"] {
  border: 1px solid rgb(240, 48, 48);
  box-shadow: 0 0 0 1px #f48f8f;
}
.names{
  margin-top:30px; 
  font-size: 15px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: #2b2b2b;
  text-shadow: 1px 2px rgb(8, 8, 8);
  font-weight: bold;
}